.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.statistic-info__container .statistic-info-list {
  padding-top: 85px;
  padding-bottom: 70px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 42px;
  justify-content: space-between;
}
@media (max-width: 960px) {
  .statistic-info__container .statistic-info-list {
    grid-template-columns: repeat(2, 1fr);
    padding-top: 45px;
    padding-bottom: 45px;
  }
}
@media (max-width: 600px) {
  .statistic-info__container .statistic-info-list {
    row-gap: 20px;
    column-gap: 10px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.statistic-info__container .statistic-info-list__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 25px;
}
@media (max-width: 600px) {
  .statistic-info__container .statistic-info-list__item {
    gap: 5px;
  }
}
.statistic-info__container .statistic-info-list .statistic-info-item__measurement {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  position: relative;
  bottom: 13px;
  left: 8px;
}
@media (max-width: 600px) {
  .statistic-info__container .statistic-info-list .statistic-info-item__measurement {
    bottom: 5px;
  }
}
.statistic-info__container .statistic-info-list .statistic-info-item__title {
  text-align: center;
  font-family: "Montserrat";
  font-weight: 800;
  font-size: 72px;
  line-height: 52px;
}
@media (max-width: 600px) {
  .statistic-info__container .statistic-info-list .statistic-info-item__title {
    font-size: 42px;
    line-height: 42px;
  }
}
.statistic-info__container .statistic-info-list .statistic-info-item__description {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.02em;
  text-align: center;
  max-width: 317px;
  line-height: 20px;
}
@media (max-width: 600px) {
  .statistic-info__container .statistic-info-list .statistic-info-item__description {
    font-size: 12px;
  }
}