@import "./default/default.scss";
.statistic-info {
  &__container {
    .statistic-info-list {
      padding-top: 85px;
      padding-bottom: 70px;
      margin-left: auto;
      margin-right: auto;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 42px;
      @media (max-width: $tablet) {
        grid-template-columns: repeat(2, 1fr);
        padding-top: 45px;
        padding-bottom: 45px;
      }
      @media (max-width: $mobile) {
        row-gap: 20px;
        column-gap: 10px;
        padding-top: 30px;
        padding-bottom: 30px;
      }
      justify-content: space-between;
      &__item {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        gap: 25px;
        @media (max-width: $mobile) {
            gap: 5px;
        }
      }
      .statistic-info-item__measurement {
        @include rale-900-18;
        position: relative;
        bottom: 13px;
        left: 8px;
        @media (max-width: $mobile) {
            bottom: 5px;
        }
      }
      .statistic-info-item__title {
        text-align: center;
        @include mont-800-72;
      }
      .statistic-info-item__description {
        @include rale-400-18;
        text-align: center;
        max-width: 317px;
        line-height: 20px;
      }
    }
  }
}
